.activities {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
}

.activity {
    background: rgb(25, 25, 25);
    border: 2px dotted yellow;
    border-radius: 1rem;
    padding: 10px;
    margin: 5px;
    width: 100%;
}

.activity > * {
    margin: 10px;
    color: #fff;
}

.activity-bottom {
    display: flex;
    align-items: center;
}

.activity-bottom > img {
    width: 10rem;
    height: 10rem;
    border-radius: 1rem;
    display: block;
    object-fit: cover;
}

.activity-bottom > p {
    margin-left: 10px;
    padding: 7px;
    font-size: 0.95rem;
    line-height: 1.2;
}

@media screen and (max-width: 700px) {
    .activity-bottom {
        flex-direction: column;
    }

    .activity-bottom > img {
        width: 14rem;
        height: 14rem;
        max-width: 50%;
    }
}